<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <editor ref="editor" :markdown="currentMarkdown" :enableHtml="enableHtml"></editor>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import editor from './components/ResumeEditor'
  import './assets/reset.css'
  export default {
    name: 'app',
    components: {
      StyleEditor,
      editor
    },
    data() {
      return {
        interval: 60,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* Hello ,Chafferer
* 
* 
* 
* ,
* Emmm... 
*/

/* begin coding */
* {
  transition: all .3s;
}
/* ,, */
html {
  color: rgb(255,69,0);
  background: rgb(255,182,193);
}
/*  */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  overflow: auto;
  width: 90vw;
  margin: 2.5vh 5vw;
  height: 90%;
}

.styleEditor {
  height: 50%;
  position: fixed; left: 0; top: 0;
}
/*  */
.token.selector{
  color: rgb(106,90,205);
}
.token.property{
  color: rgb(255,0,255);
}
.token.punctuation{
  color: white;
}
.token.function{
  color: rgb(219,112,147);
}

/* 3D */
html{
  perspective: 1000px;
}
.styleEditor {
  transform: rotateX(-10deg) translateZ(-50px) ;
}

/* "", */
.editor{
  position: fixed;
  top:50%; left: 0;
  padding: .5em;  margin: 2.5vh;
  width: 90vw; height: 45%;
  border: 1px solid; border-color: rgba(255,52,179,0.5);
  background: rgba(255,165,0,0.3); color: rgb(148,0,211);
  overflow: auto;
}

/* , */


`,
          `
/* ps:""
 * HTML,
 * , Emmm...HTML
 */
`
          ,
          `
/*  */
.editor {
  background: rgba(255,165,0,0.3); color: rgb(148,0,211);
  padding: .5em;
  margin: 2.5vh 5vw;
  top:5%; left: 0;
  width: 90vw; height: 90%;
  position: fixed;
  border: 1px solid; border-color: rgba(255,52,179,0.5);
  overflow: auto;
}
.styleEditor {
  opacity: 0;
}
.editor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
`],
        currentMarkdown: '',
        fullMarkdown: `
<h2>Dear :</h2></br>
<article>

<p>&nbsp;&nbsp;,



</p>

<p>&nbsp;&nbsp;,
,,
,,,


</p>

<p>&nbsp;&nbsp;
,,
,
,, >_<
</p>

<p>&nbsp;&nbsp;
,,,

</p>

<p>&nbsp;&nbsp;,,
,,
,,
,
</p>

<p><font size="3" color=rgb(213,26,33)>

</font></p>
<p><font size="3" color=rgb(213,26,33>
,
</font></p>
<p><font size="3" color=rgb(213,26,33>

</font></p>
<p><font size="3" color=rgb(213,26,33>

</font></p>
<p><font size="3" color=rgb(213,26,33>

</font></p>

</article>

`
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0)
        await this.progressivelyShowResume()
        await this.progressivelyShowStyle(1)
        await this.showHtml()
        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          this.$nextTick(() => {
            this.$refs.editor.goTop()
          })
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) { return }
            //  n  style 
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 1) || ' '
              this.currentStyle += char
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.editor) {
                this.$nextTick(() => this.$refs.editor.goBottom())
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh; position: relative;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }

</style>



